* {
  margin: 0px;
  padding: 0px;
}
@media (min-width: 320px) and (max-width:1440px) {
  .page {
    display: grid;
    justify-content: center;
    font-family: "Raleway", sans-serif;
    background-color: brown;
    width: 100%;
    height: 100vh;
  }
  .h1 {
    color: rgb(235, 231, 231);
    font-size: 27px;
    margin-top: 100px;
    display: grid;
    justify-content: center;
    line-height: 0.5px;
  }
  .spanOne {
    display: grid;
    justify-content: center;
  }
  .users-img {
    margin-top: 10px;
  }
  span {
    display: flex;
    justify-content: space-evenly;
    margin-top: 30px;
    list-style: none;
  }
  #users {
    border-radius: 8px;
    width: 40px;
    height: 40px;
  }
  p {
    margin-top: 10px;
    font-size: 14px;
    color: rgb(156, 152, 152);
  }
  .btn {
    display: grid;
    justify-content: space-evenly;
    margin-top: 150px;
  }
  button {
    width: 260px;
    height: 50px;
    border-radius: 5px;
    font-size: 18px;
    border: 2px solid #fff;
    background-color: rgb(22, 21, 21);
    color: #fff;
    font-weight: bolder;
    font-family: monospace;
  }
  h6 {
    color: #fff;
    font-size: 20px;
    text-align: center;
  }
  #Link {
    text-decoration: none;
    color: #fff;
    font-weight: bolder;
    font-family: monospace;
    font-size: 18px;
  }
}

@media (min-width: 280px) and (max-width: 1440px) {
    .card-section{
      display: grid;
      justify-content: center;
    }
    .link{
      display: flex;
      justify-content: space-between;
      margin-top: 20px;
    }
    .link-home{
      text-decoration: none;
      color: brown;
      font-size: 22px;
      -webkit-font-feature-settings: "smcp";
              font-feature-settings: "smcp";
      font-variant: small-caps;
    }
    .card-container {
      font-family: "Raleway", sans-serif;
      background-color: brown;
      border: 1px solid #7f7f7f7f;
      width: 300px;
      height: 380px;
      margin-top: 30px;
      text-align: center;
      border-radius: 5px;
    }
    .image-container{
      display: grid;
      justify-content: center;
    }
    .image-container img {
      margin-top: 40px;
      overflow: hidden;
      height: 90px;
      width: 90px;
      border-radius: 130px;
      object-fit: cover;
    }
    .card-ctn {
      margin: 0rem;
      margin-top: 0.5rem;
    }
    .title{
      font-family: "Raleway", sans-serif;
      font-size: 15px;
      color: brown;
      margin-top: 5px;
    }
    .text p {
      font-family: "Raleway", sans-serif;
      margin-top: 20px;
      font-size: 15px;
      color: #fff;
    }
    .button {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 40px;
    }
    .card-container a:link {
      text-decoration: none;
      padding-bottom: -100px;
    }
    .custom-btn {
      text-align: center;
      font-size: 15px;
      color: white;
      margin: 1rem 0.5rem;
      padding: 0.8em 0;
      transition: all 0.1s ease-in-out;
    }
    .custom-btn:hover {
      background-color: black;
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
    }
  }
  
